import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector, reset } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { renderField, renderFieldSelect } from "../../renderField";
import Skeleton from "react-loading-skeleton";
import { getListBarang } from "../../actions/kodeBarangAction";
import {
  calculate_barcode_level2,
  calculate_barcode_level3,
  setFocus,
} from "../../helper";
import { AxiosMasterGet } from "../../Axios";
import Swal from "sweetalert2";
import { getListTransaksiPenjualan } from "../../actions/transaksiPenjualanAction";
import { getTotalNW } from "../../actions/returnPenjualanAction";
import { gagal, warning } from "../../Alert";
import { getNetto } from "../../actions/LogicAction";
import { hideModal } from "../../actions/ModalAction";
import ReactTooltip from "react-tooltip";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormTransaksiReturn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kodeBarang: [],
      no_aksesoris: "col-lg-12 row",
      aksesoris: "col-lg-12 row d-none",
      isBarangToko : true
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
    // getTimbangan(this.props.change);
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
    this.props.change("discGlobalGr", this.props.discGlobalGr);
    this.props.change(
      "nettoDiscGlobalExt",
      (this.props.netto - this.props.discGlobalGr).toFixed(3)
    );
  }

  pilihBarang(data) {
    if (
      this.state.kondisi_barang === undefined ||
      this.state.kondisi_barang === ""
    ) {
      warning("Mohon pilih kondisi barang dahulu");
      return false;
    }
    let param = data;
    let hasil = this.props.KodeBarangList.filter(
      (data) => data.kode_jenis === param
    );
    let customer = localStorage.getItem("customer_1") || "-";
    AxiosMasterGet(
      `/kelompok-harga/get/by-jenis-and-customer/${customer}&${data}&PUSAT`
    ).then((res) => {
      if (res.data?.return_normal === undefined) {
        warning(
          "Kadar return belum ditentukan, silahkan tentukan di kelompok harga"
        );
        this.props.change("kodeBarang");
        return false;
      }
      if (res.data?.kode_kelompok === "AKSESORIS") {
        this.setState({
          no_aksesoris: "col-lg-12 row d-none",
          aksesoris: "col-lg-12 row",
        });
      } else {
        this.setState({
          no_aksesoris: "col-lg-12 row",
          aksesoris: "col-lg-12 row d-none",
        });
      }
      this.setState({
        kodeJenis: hasil[0].kode_jenis,
        namaJenis: hasil[0].nama_jenis,
        kadarCetak: res.data?.kode_tukar_normal,
        kadarJual:
          this.state.kondisi_barang === "NORMAL"
            ? res.data?.return_normal
            : res.data?.return_rongsok,
        kodeKategori: res.data?.kode_kategori,
        namaKategori: res.data?.nama_kategori,
        harga_jual: res.data?.harga_aksesoris,
      });
      this.setKadar();
      setFocus("P");
    });
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaKategori);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarJual);
    this.props.change("kodeKategori", this.state.kodeKategori);
    this.props.change("namaKategori", this.state.namaKategori);
    this.props.change("harga_jual", this.state.harga_jual);
  }

  setBruto() {
    this.props.change("bruto", localStorage.getItem("Timbangan"));
  }

  handleBarcode = () => (e) => {
    clearTimeout(this.delay);
    this.delay = setTimeout(() => {
      let hasil = e.target.value;
      if (hasil.length === 14) {
        AxiosMasterGet(
          "barang-set/get-by-kode-barcode-set-active/" + hasil
        ).then((res) => {
          res.data.detail_barang.forEach((data) => {
            this.handleBarcodeSet(data, res.data.kode_barcode_set);
          });
        });
        return true;
      } else {
        let split = hasil.split("X");
        this.props.change("kodeBarang", split[0]);
        this.setState({
          kodeJenis: split[0],
          kadarCetak: split[1],
          kadarJual: split[2],
        });
      }
    }, 1500);
  };

  handleBarcodeSet(data, barcode) {
    let param = data.kode_jenis;
    let hasil = this.props.KodeBarangList.filter(
      (data) => data.kode_jenis === param
    );
    AxiosMasterGet(`jenis/get/by-kode-jenis/${data.kode_jenis}`)
      .then((res) => {
        var simpan = {
          netto_disc: parseFloat(data.netto_disc || 0).toFixed(3),
          gross: parseFloat(data.gross || 0).toFixed(3), //berat + bungkus
          netto: parseFloat(data.netto || 0).toFixed(3), //murni
          qty: 1,
          k: parseFloat(data.k || 0),
          ongkos_rp: parseFloat(data.ongkos_rp || 0), //ongkos
          kadar_cetak: (hasil[0].kadar_cetak || 0).toString(),
          disc_persen: data.disc_persen || 0,
          kadar_jual: parseFloat(data.kadar_jual || 0), //harga%
          kadar_level2: res.data.level2,
          kadar_level3: res.data.level3,
          kadar_modal: hasil[0].kadar_beli, //harga%
          kode_jenis: hasil[0].kode_jenis, //kodebarang
          kode_barcode: "-",
          kode_barcode_set: barcode,
          p: parseFloat(data.p || 0),
          disc: data.disc || 0,
          pkg: parseFloat(data.pkg || 0),
          bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
        };
        var simpan_level2 = calculate_barcode_level2({
          data: data,
          hasil: hasil,
          res: res,
          kode_barcode_set: barcode,
        });
        var simpan_level3 = calculate_barcode_level3({
          data: data,
          res: res,
          hasil: hasil,
          kode_barcode_set: barcode,
        });

        var array =
          JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
        var array_level2 =
          JSON.parse(localStorage.getItem("transaksiPenjualan_level2")) || [];
        var array_level3 =
          JSON.parse(localStorage.getItem("transaksiPenjualan_level3")) || [];
        array.push(simpan);
        array_level2.push(simpan_level2);
        array_level3.push(simpan_level3);
        var GrandTotalNetto = array
          .map((list) => parseFloat(list.netto_disc))
          .reduce((a, b) => a + b, 0);
        localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
        localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
        localStorage.setItem(
          "transaksiPenjualan_level2",
          JSON.stringify(array_level2)
        );
        localStorage.setItem(
          "transaksiPenjualan_level3",
          JSON.stringify(array_level3)
        );
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        });
        this.props.dispatch(getListTransaksiPenjualan());
        this.props.dispatch(getTotalNW());
        this.props.dispatch(reset("FormTransaksiReturn"));
        this.props.dispatch(hideModal());
        this.props.dispatch(getNetto("transaksiPenjualan"));
        this.setState({
          discGlobalGr:
            (parseFloat(0) / 100) *
            parseFloat(localStorage.getItem("GrandTotalNetto")),
          discGlobalPr: 0,
          GrandTotalNetto: (
            parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
          ).toFixed(3),
        });
      })
      .catch((err) =>
        gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
      );
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-12">
            <div className="text-right">
              <button
                data-for="refresh"
                className="btn btn-circle btn-primary"
                type="button"
                data-tip
                onClick={() =>
                  this.props.dispatch(reset("FormTransaksiReturn"))
                }
              >
                <i class="fa fa-retweet" aria-hidden="true"></i>
                <ReactTooltip id="refresh" type="dark" effect="solid">
                  <span>Refresh</span>
                </ReactTooltip>
              </button>
            </div>
          </div>
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-lg-12 mb-5 justify-content-center">
          <div className="col-md-3 col-lg-4">
            {String(this.props.KodeBarangList) !== "" ? (
              <Field
                name="type_barang"
                label="Type Barang"
                component={renderFieldSelect}
                className="form-control"
                onChange={(data) =>
                  {
                    if(data === "BARANG NON AYU"){
                      this.props.dispatch(reset("FormTransaksiReturn"))
                    }
                    this.setState({
                      isBarangToko: data === "BARANG AYU",
                    })
                   }
                }
                options={[
                  {
                    value: "BARANG AYU",
                    name: "BARANG AYU",
                  },
                  {
                    value: "BARANG NON AYU",
                    name: "BARANG NON AYU",
                  },
                ]}
              />
            ) : (
              <Skeleton width={"100%"} height={50} />
            )}
          </div>
          </div>
          <div  className={
                  this.state.isBarangToko
                    ? "col-md-3 form-group"
                    : "col-md-3 form-group d-none"
                }>
            {String(this.props.KodeBarangList) !== "" ? (
              <Field
                name="kondisi_barang"
                label="Kondisi Barang"
                component={renderFieldSelect}
                className="form-control"
                onChange={(data) =>
                  this.setState({
                    kondisi_barang: data,
                  })
                }
                options={[
                  {
                    value: "NORMAL",
                    name: "NORMAL",
                  },
                  {
                    value: "RONGSOK",
                    name: "RONGSOK",
                  },
                ]}
              />
            ) : (
              <Skeleton width={"100%"} height={50} />
            )}
          </div>
          <div  className={
                  this.state.isBarangToko
                    ? "col-md-4 form-group"
                    : "col-md-4 form-group d-none"
                }>
            {String(this.props.KodeBarangList) !== "" ? (
              <Field
                name="kodeBarang"
                label="Kode Model"
                component={renderFieldSelect}
                className="form-control"
                onChange={(data) => this.pilihBarang(data)}
                onBlur={this.setKadar()}
                options={this.props.KodeBarangList.filter(
                  (data) =>
                    data.kode_kelompok === "PERHIASAN" ||
                    data.kode_kelompok === "AKSESORIS"
                ).map((kode) => {
                  let data = {
                    value: kode.kode_jenis,
                    name: kode.nama_jenis,
                  };
                  return data;
                })}
              />
            ) : (
              <Skeleton width={"100%"} height={50} />
            )}
          </div>
          <div
            className={
              this.state.no_aksesoris !== "col-lg-12 row"
                ? "col-md-3 col-lg-3 d-none"
                : this.state.isBarangToko ? "col-md-3 col-lg-3" : "col-md-3 col-lg-3 d-none"
            }
          >
            <Field
              name="kode_barocde"
              label="Kode Barcode"
              type="text"
              component={renderField}
              placeholder="Biarkan Kosong jika Tidak Digunakan"
              normalize={(value) => value && value.toUpperCase()}
              onChange={this.handleBarcode()}
            />
          </div>
          <div
            className={
              this.state.no_aksesoris === "col-lg-12 row"
                ? "col-md-2 col-lg-2 d-none"
                : "col-md-2 col-lg-2 d-none"
            }
          >
            <Field
              name="kadar"
              label="Kode Tukar"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3 d-none">
            <Field
              name="kodeKategori"
              label="Kode Tukar"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3 d-none">
            <Field
              name="namaKategori"
              label="Kode Tukar"
              type="number"
              component={renderField}
            />
          </div>
          <div className={this.state.no_aksesoris}>
            <div className="col-md-3 col-lg-3">
              <Field name="p" label="P" type="number" component={renderField} />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field name="k" label="K" type="number" component={renderField} />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="pkg"
                label="Pkg (Gr)"
                type="number"
                component={renderField}
                onBlur={() => this.onclick1()}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="bruto"
                label="Berat (Gr)"
                type="number"
                component={renderField}
                onChange={this.onclick1()}
                // onChange={this.setBruto()}
                // onBlur={this.setBruto()}
              />
            </div>
            <div  className={
                  this.state.isBarangToko
                    ? "col-md-3 form-group"
                    : "col-md-3 form-group d-none"
                }>
              <Field
                name="kadarTransaksi"
                label="Kode Tukar"
                type="number"
                component={renderField}
                onBlur={() => this.onclick1()}
              />
            </div>
            <div className={
                  this.state.isBarangToko
                    ? "col-md-3 form-group"
                    : "col-md-3 form-group d-none"
                }>
              <Field
                name="netto"
                label="Netto (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className={
                  this.state.isBarangToko
                    ? "col-md-3 form-group"
                    : "col-md-3 form-group d-none"
                }>
              <Field
                name="gross"
                label="Gross (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className={
                  this.state.isBarangToko
                    ? "col-md-3 form-group"
                    : "col-md-3 form-group d-none"
                }>
              <Field
                name="discGlobalPr"
                label="Diskon (%)"
                type="number"
                component={renderField}
                onBlur={() => this.onclick1()}
              />
            </div>
            <div className={
                  this.state.isBarangToko
                    ? "col-md-3 form-group"
                    : "col-md-3 form-group d-none"
                }>
              <Field
                name="discGlobalGr"
                label="Diskon (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className={
                  this.state.isBarangToko
                    ? "col-md-3 form-group"
                    : "col-md-3 form-group d-none"
                }>
              <Field
                name="nettoDiscGlobalExt"
                label="Netto - Diskon (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className={
                  this.state.isBarangToko
                    ? "col-md-3 form-group"
                    : "col-md-3 form-group d-none"
                }>
              <label htmlFor="">Ongkos (Rp)</label>
              <Field
                name="rp"
                component="input"
                type="tel"
                className="form-control"
                {...currencyMask}
              />
            </div>
          </div>
          <div className={this.state.aksesoris}>
            <div className="col-md-3 col-lg-3">
              <Field
                name="qty"
                label="Qty"
                type="number"
                component={renderField}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <label htmlFor="">Harga Jual</label>
              <Field
                name="harga_jual"
                component="input"
                type="tel"
                className="form-control"
                {...currencyMask}
              />
            </div>
          </div>
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
      </form>
    );
  }
}

FormTransaksiReturn = reduxForm({
  form: "FormTransaksiReturn",
  enableReinitialize: true,
})(FormTransaksiReturn);
const selector = formValueSelector("FormTransaksiReturn"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi, pkg, discGlobalPr } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "pkg",
    "discGlobalPr"
  );
  return {
    netto: (
      parseFloat(bruto || 0) *
      (parseFloat(kadarTransaksi || 0) / 100)
    ).toFixed(3),
    gross: parseFloat(bruto || 0) + parseFloat(pkg || 0),
    discGlobalGr: (
      parseFloat(bruto || 0) *
      (parseFloat(discGlobalPr || 0) / 100)
    ).toFixed(3),
    KodeBarangList: state.provinsi.kodeBarang,
    initialValues: {
      discGlobalPr: 0,
      p: 0,
      k: 0,
      pkg: 0,
      bruto: 0,
      type_barang: "BARANG AYU"
    },
  };
})(FormTransaksiReturn);
